@keyframes fadeIn {
    from {
      opacity: 0;
    }
    
    to {
      opacity: 1;
    }
  }

  .fadeIn {
      @include fadeInAnimation;
  }
  

@keyframes spinIn {
  from {
    border-radius: 50%;
    transform: scale(0.5)  translateY(1600px);
  }
  
  to {
    border-radius: 0;
    transform: scale(1)  translateY(0);
  }
}

.spin-interval-1 {
  animation: spinIn;
  animation-duration: .6s;
  animation-iteration-count: 1;
}

.spin-interval-2 {
  animation: spinIn;
  animation-duration: .8s;
  animation-iteration-count: 1;
}

.spin-interval-3 {
  animation: spinIn;
  animation-duration: 1s;
  animation-iteration-count: 1;
}

.spin-interval-4 {
  animation: spinIn;
  animation-duration: 1.2s;
  animation-iteration-count: 1;
}

