.text-center {
    text-align: center;
}

.border-bottom {
    border-bottom: 1.3px solid rgb(220, 220, 220);
}

.border-top {
  border-top: 1.3px solid $light;
}

.clip {
  clip-path: polygon(50% 0, 100% 0, 100% 85%, 50% 100%, 0 85%, 0% 43%, 0 0);
}

.text-gradient {
  @include textGradient($orange, $pink);
}

.text-gradient-ie { //IE Explorer doesn't support background clip for gradient
  color: $lighter;
}

.bg-image {
  background-image: linear-gradient( to left,rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 1)), url('../assets/home5.jpg') ;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  
}

.bg-dark {
    background: $dark-heading
}

.bg-light {
  background: $lighter;
}



.gradient {
  background: linear-gradient(to left,$orange, $pink)
}

.bg-white {
    background: white;
}





.color-light {
  color: $light;
}

.color-lighter {
  color: $lighter;
}

.font-light {
    font-weight: lighter;
}

