html {
  font-size: 62.5%; }
  @media (max-width: 160em) {
    html {
      font-size: 95%; } }
  @media (max-width: 90em) {
    html {
      font-size: 90%; } }
  @media (max-width: 64em) {
    html {
      font-size: 85%; } }
  @media (max-width: 48em) {
    html {
      font-size: 80%; } }
  @media (max-width: 26.5625em) {
    html {
      font-size: 75%; } }

* {
  padding: 0;
  margin: 0;
  font-family: 'PT Sans', sans-serif;
  color: #454545; }

.border-right {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%; }
  @media (max-width: 26.5625em) {
    .border-right {
      display: none; } }
  .border-right div {
    width: 1px;
    height: 100%;
    border-right: 1.3px solid gainsboro; }
